.page.positions .top-container {
  display: inline-block;
  height: 25%;
  vertical-align: top; }

.page.positions .bottom-container {
  position: relative;
  height: 75%; }
  .page.positions .bottom-container:before {
    content: '';
    width: 100%;
    height: 16px;
    position: absolute;
    z-index: 100;
    top: 0;
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#ffffff+0,95b7e5+99 */
    background: -moz-linear-gradient(bottom, rgba(255, 255, 255, 0) 0%, #95b7e5 99%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(bottom, rgba(255, 255, 255, 0) 0%, #95b7e5 75%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to top, rgba(255, 255, 255, 0) 0%, #95b7e5 75%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */ }
  .page.positions .bottom-container:after {
    content: '';
    width: 100%;
    height: 32px;
    position: absolute;
    z-index: 100;
    bottom: 0;
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#ffffff+0,95b7e5+99 */
    background: -moz-linear-gradient(top, rgba(255, 255, 255, 0) 0%, #95b7e5 99%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 0%, #95b7e5 75%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, #95b7e5 75%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */ }

.page.positions .positions-container {
  position: relative;
  height: 100%;
  overflow-y: scroll; }

.page.positions .position {
  position: relative;
  width: 50%;
  display: inline-block;
  padding: calc(15vw - 16px) 8px 0 8px;
  margin-bottom: 64px;
  animation-name: fade-in-and-grow;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
  opacity: 0;
  /* nudge the first row down to leave room for the top gradient*/
  /* and the last row for the same on the bottom */ }
  .page.positions .position:first-child, .page.positions .position:nth-child(2) {
    margin-top: 16px; }
  .page.positions .position:nth-child(2) {
    animation-delay: 0.2s; }
  .page.positions .position:nth-child(3) {
    animation-delay: 0.4s; }
  .page.positions .position:nth-child(4) {
    animation-delay: 0.6s; }
  .page.positions .position:nth-child(5) {
    animation-delay: 0.8s; }
  .page.positions .position:nth-child(6) {
    animation-delay: 1s; }
  .page.positions .position .speech-bubble {
    width: 100%;
    text-align: center;
    padding: 16px; }
  .page.positions .position .explore-score {
    position: absolute;
    z-index: 10;
    top: 0px;
    right: 15vw; }
  .page.positions .position .position-count {
    position: absolute;
    z-index: 20;
    top: 0px;
    right: 0; }
  .page.positions .position .position-count.circle-s.circle p {
    margin: 0; }
    .page.positions .position .position-count.circle-s.circle p:first-child {
      font-size: 24px; }
    .page.positions .position .position-count.circle-s.circle p:nth-child(2) {
      font-size: 12px; }
