.page.welcome {
  overflow: hidden; }
  .page.welcome .top-container {
    z-index: 10;
    height: 30%; }

.explore-button {
  margin-top: 35%; }

.title-image {
  margin-top: 32px; }

.bottom-container {
  position: relative;
  height: 65%;
  z-index: 1; }

.welcome-animation {
  position: absolute;
  width: 60%;
  height: 100%;
  right: 0;
  bottom: 0; }
  .welcome-animation .animation-container {
    position: absolute;
    height: 100%;
    width: 100%; }
  .welcome-animation .speech-bubble-container {
    position: absolute;
    animation-name: fly-in-bottom-with-shadow;
    animation-duration: 1.5s;
    animation-fill-mode: forwards;
    will-change: transform;
    transform: translateY(100vh); }
    .welcome-animation .speech-bubble-container:nth-child(1) {
      top: -5%;
      right: 5%;
      animation-delay: 0s; }
      .welcome-animation .speech-bubble-container:nth-child(1) .speech-bubble {
        transform: scale(0.3); }
    .welcome-animation .speech-bubble-container:nth-child(2) {
      top: 5%;
      right: 30%;
      animation-delay: 0.1s; }
      .welcome-animation .speech-bubble-container:nth-child(2) .speech-bubble {
        transform: scale(0.2); }
    .welcome-animation .speech-bubble-container:nth-child(3) {
      top: 20%;
      right: 40%;
      animation-delay: 0.2s; }
      .welcome-animation .speech-bubble-container:nth-child(3) .speech-bubble {
        transform: scale(0.75); }
    .welcome-animation .speech-bubble-container:nth-child(4) {
      top: 20%;
      right: -15%;
      animation-delay: 0.3s; }
      .welcome-animation .speech-bubble-container:nth-child(4) .speech-bubble {
        transform: scale(0.5); }
    .welcome-animation .speech-bubble-container:nth-child(5) {
      top: 50%;
      right: -16%;
      animation-delay: 0.4s; }
      .welcome-animation .speech-bubble-container:nth-child(5) .speech-bubble {
        transform: scale(0.8); }
    .welcome-animation .speech-bubble-container:nth-child(6) {
      top: 35%;
      right: 10%;
      animation-delay: 0.5s; }
      .welcome-animation .speech-bubble-container:nth-child(6) .speech-bubble {
        transform: scale(0.6); }
    .welcome-animation .speech-bubble-container:nth-child(7) {
      top: 65%;
      right: 40%;
      animation-delay: 0.6s; }
  .welcome-animation .shadow {
    position: absolute;
    height: 0px;
    z-index: -100;
    opacity: 0;
    box-shadow: 0px 0px 10px 3px #657cc9;
    animation-name: fade-in-delayed;
    animation-duration: 1.5s;
    animation-fill-mode: forwards;
    animation-delay: 0.5s;
    will-change: transform; }
    .welcome-animation .shadow:nth-child(1) {
      width: 50%;
      right: 50%;
      bottom: 0; }
    .welcome-animation .shadow:nth-child(2) {
      width: 30%;
      right: 5%;
      bottom: 4%; }
    .welcome-animation .shadow:nth-child(3) {
      width: 30%;
      right: 40%;
      bottom: 3%; }
  .welcome-animation .star-container {
    position: absolute;
    display: inline-block;
    opacity: 0;
    animation-name: fade-in;
    animation-fill-mode: forwards;
    animation-duration: 6s;
    animation-delay: 2.5s; }
    .welcome-animation .star-container:nth-child(1) {
      top: -10%;
      right: 30%; }
      .welcome-animation .star-container:nth-child(1) .star {
        animation-delay: 0; }
    .welcome-animation .star-container:nth-child(2) {
      top: -0%;
      right: 5%; }
      .welcome-animation .star-container:nth-child(2) .star {
        animation-delay: 1s;
        animation-duration: 1.5s; }
    .welcome-animation .star-container:nth-child(3) {
      top: 20%;
      right: 38%; }
      .welcome-animation .star-container:nth-child(3) .star {
        animation-delay: 0.2s; }
    .welcome-animation .star-container:nth-child(4) {
      top: 45%;
      right: 10%; }
      .welcome-animation .star-container:nth-child(4) .star {
        animation-delay: 0.7s;
        animation-duration: 1.5s; }
    .welcome-animation .star-container:nth-child(5) {
      top: 55%;
      right: 75%; }
      .welcome-animation .star-container:nth-child(5) .star {
        animation-delay: 0.5s; }
  .welcome-animation .star {
    animation-name: twinkle;
    animation-fill-mode: forwards;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    animation-timing-function: ease; }
